.Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
  }
  
  .Icon-container-check {
    position: relative;
    left: 12px;
  }
  .Icon-container-check--active {
    color: #4caf50;
  }
  
  .Icon-container-delete:hover {
    color: red;
  }
  .Icon-container-check:hover {
    color:#4caf50;
  }
  
  .Icon {
    width: 24px;
    height: 24px;
  }
  
