*{
  font-size: 62.5%;
  box-sizing: border-box;
  margin: 0 ;
  padding: 0;
}
body{
  background: linear-gradient(90deg, rgba(63,94,251,1) 28%, rgba(252,70,80,0.6839110644257703) 77%);
  display: grid;
  place-items: center;
  height: 100vh;

}
body > div {
  background: white;
  z-index: 0;
  height: auto;
  max-height: 800px;
  border-radius: 12px;
  position: relative;
  max-width: 600px;
}


@media (max-width: 450px) {
  body{
    background: #fff;
    display: grid;
    place-items: center;
    height: 100vh;
  
  }
  body > div {
    height: 100vh;
    border-radius: 12px;
  }
  input {
    width: 80%;
    height: 40px;
    font-size: 2rem;
    padding: 4px;
    position: relative;
    top: 5%;
    right: 5%;
    left: 5%;
  }
}