.App-header {
    width: 100%;
    min-width: 600px;
    height: 200px;
    position: relative;
    border-radius: 12px;
    overflow: hidden;
}

.custom-svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 180px;
    transform-origin: top;  
    display: block;
    z-index: -1;
}
h1 {
    font-size: 2.6rem;
    text-align:center;
    color: white;
    margin-right: 15%;
    padding:48px;
    z-index: 1;
}
img {
width:100px;
height:100px;
position: relative;
left: 80%;
bottom: 10%;
}
