input {
    width: 90%;
    height: 50px;
    font-size: 2.2rem;
    padding: 8px;
    position: relative;
    top: 35%;
    right: 5%;
    left: 5%;
  }
  
  body > div ul {
    margin: 0 auto;
    margin-bottom: 50px;
    padding: 4px;
  }
  
  body > div li {
    font-weight: bold;
    border: solid 0.5px #ff9999;
    margin-bottom: 8px;
    margin: 12px;
    padding: 4px;
    border-radius: 12px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
  }
  
  body > div li p {
    font-size: 2rem;
    padding: 8px;
  }
  
  .TodoItem-p--complete {
    text-decoration: line-through;
  }
  
  /* button */
  /* Si necesitas posicionar el botón en la esquina inferior derecha,
     envuelve el botón en un contenedor */
  .button-container {
    position: fixed;
    bottom: 25px;
    right: 25px;
    z-index: 9999; /* Add this line to bring the button to the front */
  }
  
  .CreateTodoButton {
    background-color: #ff9999;
    box-shadow: 0px 5px 25px rgba(236, 87, 99, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    transform: rotate(0deg);
    transition: transform 0.3s ease;
  }
  
  .CreateTodoButton:hover {
    transform: rotate(224deg);
  }
  @media (max-width: 450px) {
    input {
      width: 80%;
      height: 40px;
      font-size: 2rem;
      margin-top: 8px;
      padding: 4px;
      position: relative;
      top: 0;
      right:0;
      left: 8%;
    }
  }